@-webkit-keyframes glitch {
  0% {
    text-shadow: none; }
  2% {
    text-shadow: 7px 0px #f00; }
  4% {
    text-shadow: 7px 0px #f00, -10px 0px #0f0; }
  6% {
    text-shadow: 7px 0px #f00, -10px 0px #0f0, 13px 0px #00f; }
  10%, 100% {
    text-shadow: none; } }

@keyframes glitch {
  0% {
    text-shadow: none; }
  2% {
    text-shadow: 7px 0px #f00; }
  4% {
    text-shadow: 7px 0px #f00, -10px 0px #0f0; }
  6% {
    text-shadow: 7px 0px #f00, -10px 0px #0f0, 13px 0px #00f; }
  10%, 100% {
    text-shadow: none; } }

